import React from "react"
import { Container, Row, Col } from 'react-bootstrap'
import { useStaticQuery, graphql } from "gatsby"

import "./layout.css"

import FBIcon from '../images/facebook.inline.svg'
import LIIcon from '../images/linkedin.inline.svg'
import GULIcon from '../images/github-logo.inline.svg'
import GatsbyIcon from '../images/gatsbyjs.inline.svg'


const Projects = () => {
    const { site } = useStaticQuery(
        graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
          }
        }
      }
    `
    )
    return (
        <div style={{ 'maxHeight': '100px' }}>
            <Row>
                <Col xs={6} md={4}>

                    <div className='d-flex justify-content-centeralign-items-center'>
                        <span>&copy; {new Date().getFullYear()}, Jayanta Madhav</span>
                    </div>
                </Col>
                <Col xs={6} md={4} className='md'>
                    <div className='d-flex justify-content-center align-items-center'>
                        <a href='https://www.facebook.com/jayanta.madhav.5/' target='_blank'>
                            <FBIcon className='social-icons' />
                        </a>
                        <a href='https://www.linkedin.com/in/jayantamadhav/' target='_blank'>
                            <LIIcon className='social-icons' />
                        </a>
                        <a href='https://github.com/jayantamadhav' target='_blank'>
                            <GULIcon className='social-icons' />
                        </a>
                    </div>
                </Col>
                <Col md={4} className='d-none d-sm-block'>
                    <Row>
                        <Col xs={4}>
                            <div className='d-flex justify-content-center align-items-right h-100'>
                                <span className='light-text'>Built with</span>
                            </div>
                        </Col>
                            <GatsbyIcon style={{ 'maxHeight': '70px', 'maxWidth': '100px', 'marginTop': '-30px' }} />
                        <Col xs={6}>
                        </Col>
                    </Row>
                </Col>
            </Row>
            {/* <Row>
                <Col xs={6} md={4} className='light-text'> */}
            {/* </Col>
                <Col xs={6} md={4}>
                
                    <Row>
                        <Col xs={4}>
                            <Container fluid>
                            <FBIcon style={{'maxHeight' : '70px', 'maxWidth' : '70px'}}/>
                            </Container>
                        </Col>
                    </Row>
                    
                    <Container className='p-2'>
                        <div className='d-flex justify-content-start align-items-left'>
                            <div className='social-icons h-25'>
                                <a href='https://www.facebook.com/jayanta.madhav.5/' target='_blank'>
                                    <FBIcon style={{ 'maxWidth': '50px' }} />
                                </a>
                            </div>
                            <div className='social-icons h-25'>
                                <a href='https://www.linkedin.com/in/jayantamadhav/' target='_blank'>
                                    <LIIcon style={{ 'maxWidth': '50px' }} />
                                </a>
                            </div>
                            <div className='social-icons h-25'>
                                <a href='https://github.com/jayantamadhav' target='_blank'>
                                    <GULIcon style={{ 'maxWidth': '50px' }} />
                                </a>
                            </div>
                        </div>
                    </Container>
                </Col>
                <Col md={4}>
                    <Row>
                        <Col xs={6}>
                            <div className='h-100 d-flex justify-content-center align-items-center light-text'>
                                Built using
                            </div>
                        </Col>
                        <Col xs={6}>
                            <GatsbyIcon style={{ 'maxWidth': '100%'}} />
                        </Col>
                    </Row>
                </Col>
            </Row> */}
        </div>
    )
}


export default Projects
